import DashboardLayout from "@/layout/dashboard/DashboardLayout.vue";
import UnsecuredLayout from "@/layout/dashboard/ULayout.vue";
// GeneralViews
import NotFound from "@/pages/NotFoundPage.vue";

// Admin pages
const Dashboard = () => import(/* webpackChunkName: "dashboard" */"@/pages/Dashboard.vue");
const Preview = () => import(/* webpackChunkName: "dashboard" */"@/pages/Preview.vue");
const Profile = () => import(/* webpackChunkName: "common" */"@/pages/Profile.vue");
const Login = () => import(/* webpackChunkName: "common" */"@/pages/Login.vue");
const Register = () => import(/* webpackChunkName: "common" */"@/pages/Register.vue");

const routes = [
  {
	path: "/login",
	name: "login",
	component: Login,
  },
  {
	path: "/register",
	name: "register",
	component: Register,
  },
  {
    path: "/preview",
    component: UnsecuredLayout,
    children: [
      {
        path: "",
        name: "preview",
        component: Preview,
      },
    ]
  },
  {
    path: "/",
    component: DashboardLayout,
    redirect: "/preview",
    children: [
      {
        path: "dashboard",
        name: "dashboard",
        component: Dashboard,
		meta: {
		  requiresAuth: true
		}
      },
      {
        path: "profile",
        name: "profile",
        component: Profile,
		meta: {
		  requiresAuth: true
		}
      },
    ]
  },
  { 
	path: "*",
	component: NotFound,
	meta: {
	  requiresAuth: true
	} 
  },
];

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes;
